import { useRouter } from 'next/router'
import Link from 'next/link'
import Navbar from '../../components/Navbar'

export default function Style(){
  const router = useRouter()
  const { goal, experience, time, days, equipment } = router.query
  const opts = [
    {id:'strength', label:'Strength-focused'},
    {id:'hypertrophy', label:'Hypertrophy / muscle'},
    {id:'endurance', label:'Endurance / cardio'},
    {id:'balanced', label:'Balanced'}
  ]
  return (
    <>
      <Navbar />
      <main className="container p-8 max-w-3xl">
        <h2 className="text-2xl font-semibold mb-4">Pick a training style</h2>
        <div className="grid md:grid-cols-2 gap-4">
          {opts.map(o => (
            <Link key={o.id} href={`/onboard/adaptation?goal=${encodeURIComponent(goal)}&experience=${encodeURIComponent(experience)}&time=${encodeURIComponent(time)}&days=${days}&equipment=${equipment}&style=${o.id}`} className="card">
              <h3 className="font-semibold">{o.label}</h3>
            </Link>
          ))}
        </div>
      </main>
    </>
  )
}
